import * as THREE from 'three';
import { useGLTF } from '@react-three/drei';
import tower from './models/tower.glb';
import { GLTF } from 'three/examples/jsm/loaders/GLTFLoader';
import { config } from './config';

type GLTFResult = GLTF & {
  nodes: {
    tower: THREE.Mesh;
  };
  materials: { [key: string]: THREE.Material };
};

const material = new THREE.MeshToonMaterial({ color: '#00FF00' });

type TowerProps = {
  floor?: number;
};

export const Tower = ({ floor = 0 }: TowerProps) => {
  const { nodes } = useGLTF(tower) as GLTFResult;

  return (
    <mesh
      geometry={nodes.tower.geometry}
      material={material}
      position={[0, floor * config.tower.sectionHeight, 0]}
    />
  );
};
