import { useFrame } from '@react-three/fiber';
import { useEffect, useRef, useState } from 'react';
import * as THREE from 'three';
import { config } from './config';
import { lerp, mapInterval } from './utils';

const { sectionHeight, radius, eyeLevel } = config.tower;

export const CameraDummy = () => {
  const [currentFloor, setCurrentFloor] = useState(0);
  const targetPosY = useRef(0);
  const coefRotY = useRef(0);

  // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
  const group = useRef<THREE.Group>(null!);

  useFrame(() => {
    targetPosY.current = currentFloor * sectionHeight + eyeLevel;
    group.current.position.y = lerp(group.current.position.y, targetPosY.current, 0.02);
    coefRotY.current = (targetPosY.current - group.current.position.y) / sectionHeight;
    group.current.rotation.y = mapInterval(0, 1, Math.PI * 2, 0, coefRotY.current);
  });

  useEffect(() => {
    const listener = (e: KeyboardEvent) => {
      if (e.key === 'ArrowUp') {
        setCurrentFloor((v) => v + 1);
      }
      if (e.key === 'ArrowDown') {
        setCurrentFloor((v) => v - 1);
      }
    };
    document.addEventListener('keydown', listener);

    return () => document.removeEventListener('keydown', listener);
  }, []);

  return (
    <group ref={group} position={[0, eyeLevel, 0]}>
      <mesh position={[0, 0, radius]}>
        <boxGeometry args={[0.2, 0.2, 0.2]} />
        <meshStandardMaterial color="indianred" />
      </mesh>
      <mesh position={[0, 0, radius - 0.1]}>
        <boxGeometry args={[0.1, 0.1, 0.1]} />
        <meshStandardMaterial color="salmon" />
      </mesh>
    </group>
  );
};
